import axios from 'axios'

const API_KEY = (process.env.REACT_APP_WEATHER_API_KEY)
const apiKey = API_KEY
// const API_KEY2 = process.env.REACT_APP_WEATHER_API_KEY_2;
export const weatherService = {
    callCityWeather,
    callCities
}

export async function callCities(value) {

    try{
    var citiesData = await axios.get(`https://dataservice.accuweather.com/locations/v1/cities/autocomplete?apikey=${apiKey}&q=${value.cityName}`)
    const cities = citiesData.data;
    return cities
    }
    catch(err){
        console.log('Err',err)
    }
}
export async function callCityWeather(value) {
    try{
    var citiesData = await axios.get(`https://dataservice.accuweather.com/forecasts/v1/daily/5day/${value}?apikey=${apiKey}`)
    const cities = citiesData.data;
    return cities
    }
    catch{
        console.log('Err')
    }
}
